const util = require('../../components/zwy/util/util.js');
import moment from 'moment'
import refresh from '../../components/zwy/refresh.vue';
import navTab from '../../components/zwy/navTab.vue';
import clickCircle from '../../components/zwy/clickCircle.vue';

export default {
	components: {
		refresh,
		navTab,
		clickCircle
	},
	data() {
		return {
			toView: '', //回到顶部id
			isX: 999, //放在store统一管理
			isY: 999, //放在store统一管理
			tabTitle: ['我的日志', '我的教室'], //导航栏格式 --导航栏组件
			currentTab: 0, //sweiper所在页
			pages: [1, 1], //第几个swiper的第几页
			list: [], //数据格式
			myLogList: [],
			roomList: [],
			userInfo: {},
			myRoomLog: {},
			modalName: ""
		}
	},
	computed: {

	},
	// 转化毫秒时间戳为日期格式
	filters: {
		getYMD: function(value) {
			return moment(value).format('YYYY-MM-DD')
		},
		getHm: function(value) {
			return moment(value).format('HH:mm')
		},
		getDay: function(value) {
			return moment(value).format('E')
		},
		getLength: function(value) {
			return value.length
		}
	},
	onLoad() {

	},
	onShow() {
		this.isLogin()
		if (this.userInfo && this.userInfo.realNameInfo != null) {
			this.getLog()
		} else if (this.userInfo && this.userInfo.realNameInfo == null) {
			uni.showModal({
				title: "请先实名",
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
						console.log("确定了啊")
						uni.switchTab({
							url: "/pages/profile/profile"
						})
					}

				}
			})
		}

	},
	methods: {
		conClick(e) {
			this.$refs.clickCircle.conClick(e);
		},
		// 判断登录
		isLogin() {
			this.userInfo = global.isLogin();
			console.log(this.userInfo)
			if (!this.userInfo) {
				uni.showModal({
					title: "请先登录",
					showCancel: false,
					success: function(res) {
						if (res.confirm) {
							console.log("确定了啊")
							uni.switchTab({
								url: "/pages/profile/profile"
							})
						}

					}
				})
			}
		},

		getLog() {
			var that = this;

			uni.request({
				url: "https://impl.lifeisgg.online/attendance/log/getLog",
				data: {
					"uId": that.userInfo.uId,
					"rId": ""
				},
				success(res) {
					console.log(res.data)
					that.myLogList = res.data.logList
					// for(let i = 0;i<that.myLogList.length;i++){
					// 	that.myLogList[i].lDate = moment(res.data.logList[0].lDate).format('YYYY-MM-DD-dd HH:mm:ss')
					// }				
					that.roomList = res.data.roomList
					that.list[0] = that.myLogList.reverse()
					that.list[1] = that.roomList.reverse()
					console.log(that.list)
					uni.showToast({
						title: "获取日志成功"
					})

				}
			})


		},
		// 获取我的教室日志详细信息
		getMyRoomDetail(rId) {
			console.log(rId)
			var that = this
			if (that.userInfo.uId && rId) {
				uni.request({
					url: "https://impl.lifeisgg.online/attendance/log/getLog",
					data: {
						"uId": that.userInfo.uId,
						"rId": rId
					},
					success(res) {
						console.log(res)
						that.myRoomLog = res.data.logList.reverse()
						uni.showToast({
							title: "获取成功"
						})

					}
				})
			} else {
				uni.showToast({
					title: "获取失败"
				})
			}
		},

		showModal(e) {
			console.log(e.currentTarget.dataset)
			if (e.currentTarget.dataset.rid) {
				this.getMyRoomDetail(e.currentTarget.dataset.rid)
			}
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},


		// 逆序输出日志
		// reverseLog: function(value) {
		// 	var list = this.list[0].reverse()
		// 	return this.list
		// },



		toTop() {
			this.toView = ''
			setTimeout(() => {
				this.toView = 'top' + this.currentTab
			}, 10)
		},
		changeTab(index) {
			this.currentTab = index
		},
		// 其他请求事件 当然刷新和其他请求可以写一起 多一层判断。
		isRequest(pages) {
			return new Promise((resolve, reject) => {
				this.pages[this.currentTab]++
				var that = this
				setTimeout(() => {
					uni.hideLoading()
					uni.showToast({
						icon: 'none',
						title: `请求第${that.currentTab + 1 }个导航栏的第${that.pages[that.currentTab]}页数据成功`
					})
					let newData = ['新数据1', '新数据2']
					resolve(newData)
				}, 1000)
			})
		},
		// swiper 滑动
		swiperTab: function(e) {
			var index = e.detail.current //获取索引
			this.$refs.navTab.longClick(index);
		},
		// 加载更多 util.throttle为防抖函数
		lower1: util.throttle(function(e) {
			console.log(`加载${this.currentTab}`) //currentTab表示当前所在页数 根据当前所在页数发起请求并带上page页数
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			this.isRequest().then((res) => {
				let tempList = this.list
				tempList[this.currentTab] = tempList[this.currentTab].concat(res)
				console.log(tempList)
				this.list = tempList
				this.$forceUpdate() //二维数组，开启强制渲染
			})
		}, 300),
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e);
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
		isRefresh() {
			setTimeout(() => {
				uni.showToast({
					icon: 'success',
					title: '刷新成功'
				})
				this.$refs.refresh.endAfter() //刷新结束调用
			}, 1000)
		}
	}
}
